import cp_router from './cp_router.js'/*云平台项目路由*/
import cp_map from './cp_map.js'/*云平台项目配置参数*/
export default function (pro) {
  console.log(pro + ":项目");
  switch (pro) {
    case 'cp':
      return { router: cp_router, map: cp_map };
    default:
      break;
  }
}



