import Vue from 'vue'
import Vuex from 'vuex'
import APICONFIG from "../config/api";
import VueResource from 'vue-resource';
Vue.use(Vuex)
Vue.use(VueResource);
const state = {
  accessManage:{
     //门禁系统设备类型
  deviceType:"1",
  //门禁系统设备列表
  deviceList:[],
  // 分组数据
  groupData:{},
  netAPI:'',
  addList:[],
  deviceAlreadyList:[]//新增门禁设备数据备份
  },
  MonitoringManage:{
   //智能安防设备类型
   deviceType:"1",
   //智能安防设备列表
   deviceList:[],
   },
  //  批量导入设备时用到的设备组编号
   importDeviceNo: 1005,
   importDeviceType: 1
}
const getters={

}
const actions={

}
const mutations={
  freshData:function(state){
    var netAPI2 = APICONFIG().get("accessSystem")
    const promise = new Promise((resolve, reject) => {
      Vue.http
        .get(netAPI2.accessDevice.findAllDevice, {
          params: {
            groupNo: state.accessManage.groupData.groupNo,
            type: state.accessManage.deviceType
          }
        })
        .then(res => {
          if (res.body.status == 200) {
            state.accessManage.deviceList = res.body.data;
            resolve(res.body.data)
          } else {
            this.$message.error(res.body.message);
          }
        });
    });
    promise.then(res=>{
      Vue.http
        .get(netAPI2.accessDevice.findDeviceAll, {
          params: {
            pageSize: 1000,
            pageNo: 1
          }
        })
        .then(res => {
          state.accessManage.deviceAlreadyList = res.body.data
          for (const key in res.body.data) {
            if (res.body.data.hasOwnProperty(key)) {
              const element = res.body.data[key];
              if(state.accessManage.addList.length< res.body.data.length){
                state.accessManage.addList.push({
                  name: element.deviceName,
                  id: element.deviceId,
                  closable:false
                });
              }

              for (const key2 in   state.accessManage.deviceList) {
                if (  state.accessManage.deviceList.hasOwnProperty(key2)) {
                  const device =  state.accessManage.deviceList[key2];
                  if (element.deviceId == device.id) {
                    // device.isAdd = false;

                    Vue.set(device, "isAdd", "true");
                  }
                }
              }
            }
          }
        });
    
    })
  },
  // 更改导入设备类型
  changeimportDeviceType(state,data){
    // console.log(state)
    // console.log(data)
    state.importDeviceType = data
  }
}
export default new Vuex.Store({
   state,getters,actions,mutations
});



