import 'babel-polyfill';
import Vue from 'vue';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import App from './App';
import Router from './router';
import VueResource from 'vue-resource';
import '../static/directives.js';
// import api from './config/api.js';
import tableHead from './config/tableType.js';
import treeBuilder from '../static/treeBuilder.js';
import Vuex from 'vuex';
// import store from '../src/store/store.js';
const store = () => require('../src/store/store.js')
import echarts from 'echarts';
import VueI18n from 'vue-i18n';
import locale from 'element-ui/lib/locale'
import enLocale from 'element-ui/lib/locale/lang/en'
import zhLocale from 'element-ui/lib/locale/lang/zh-CN'
// import '../static/filterFun'

Vue.use(Vuex)

Vue.use(VueResource);
Vue.use(ElementUI, { size: 'samll', zIndex: 3000 });
Vue.use(VueI18n);
const i18n = new VueI18n({
  locale: 'en', // 语言标识
  messages: {
    'en': Object.assign(require('../src/assets/lang/en'), enLocale),
    'zh': Object.assign(require('../src/assets/lang/zh'), zhLocale),
  }
})
locale.i18n((key, value) => i18n.t(key, value));
Vue.use(ElementUI, {i18n: (key, value) => i18n.t(key, value), zIndex: 3000});
Vue.prototype.$echarts = echarts;
let routerObj = Router(process.env.PRO_ENV);

let rMap = routerObj.map;
let router = routerObj.router;
//全局API
// Vue.prototype.$api = api;
//全局表头统一管理
Vue.prototype.$tableHead = tableHead;
//动态路由
Vue.prototype.$routerMap = rMap;
Vue.config.productionTip = false;
let permissionsRouting = localStorage.getItem('route');
// 全局注册EventBus
Vue.prototype.$EventBus = new Vue()

if (permissionsRouting) {
  
  router.options.routes[1].children = treeBuilder(JSON.parse(permissionsRouting));
  router.addRoutes(router.options.routes);
}

/* eslint-disable no-new */
// 动态获取ip
function getIp() {
  return new Promise((resolve, reject) => {
    if(process.env.NODE_ENV === 'development') {
      localStorage.setItem('ipLoc', 'http://192.168.100.10:18081')
      console.log(22222)
      resolve()
    } else {
      if(typeof qt != "undefined") {
        new QWebChannel(qt.webChannelTransport, function(channel) {
            window.Bridge = channel.objects.Bridge;
                Bridge.sigHostNameChanged.connect(function(dataObj) { //连接Qt中brideg类的信号并定义执行代码
                //console.log(JSON.stringify(dataObj))
                console.log(dataObj)
                localStorage.setItem('ipLoc', dataObj)
                resolve()
            });
            Bridge.onInitsized(); //调用Qt内的函数
        });
      }
    }
  })
}

getIp().then(()=> {
  Vue.prototype.$store = store
  let vue = new Vue({
    el: '#app',
    router,
    store,
    i18n,
    components: { App },
    template: '<App/>'
  })
  // 路由拦截器
  Vue.http.interceptors.push((req, next) => {
    req.headers.set('token', '7d1e52d3cf0142e19b5901eb1ef91372');
    if(i18n.locale == 'en') {
      req.headers.set('Accept-Language', `en-US`)
    }else{
      req.headers.set('Accept-Language', `zh-CN`)
    }
  })
})




