import Vue from 'vue'
import Router from 'vue-router'
/*
云平台
*/
const Main = () => import('@/components/pages/project/cp/Main.vue');
const login = () => import('@/components/pages/public/login.vue');
const MonitoringLogin = () => import('@/components/pages/public/MonitoringLogin.vue')
/*
功能模块
*/
// const acs_main = () => import('@/components/pages/project/cp/functional_module/access_control_system/Main.vue');
// const acs_ace_manage = () => import('@/components/pages/project/cp/functional_module/access_control_system/ace_manage/index.vue');
// const acs_ace_rules_passage = () => import('@/components/pages/project/cp/functional_module/access_control_system/ace_manage/rules_passage.vue');
const access_manage = ()=>import('@/components/pages/project/cp/functional_module/access_system/app.vue')
const access_system = ()=>import('@/components/pages/project/cp/functional_module/access_system/main.vue')

const securitymonitoring_manage = ()=>import('@/components/pages/project/cp/functional_module/monitoring_system/app.vue')
const monitoring_system = ()=>import('@/components/pages/project/cp/functional_module/monitoring_system/main.vue')
const deviceControl = ()=>import('@/components/pages/project/cp/device_manage/device_control.vue')
const peopleList = ()=>import('@/components/pages/public/people_manage/peopleList.vue')

Vue.use(Router);
export default new Router({
  routes: [
    {
      path: '/',
      name: '主页',
      meta: { show: true,enName:'Home' },
      component: deviceControl,
    },
    {
      path: '/monitoringLogin',
      name: 'monitoringLogin',
      component: MonitoringLogin
    },
    {
      path: '/deviceManage/deviceControl',
      name: 'deviceControl',
      component: deviceControl
    },
    {
      path: '/peopleManage/list',
      name: 'peopleList',
      component: peopleList
    },
    {
      path: "*",
      redirect: "/deviceManage/deviceControl"
    },
    {
      path: '/accessSystem',
      name: '考拉智能门禁系统',
      component: access_system,
      children: [
        {
          name: '门禁管理',
          path: '/accessSystem/access_manage',
          component: access_manage,
          meta: { show: true },
          children: [
            { name: '首页', path: '/accessSystem/access_manage/index', component: () => import('@/components/pages/project/cp/functional_module/access_system/index.vue'), meta: { show: true,enName:'Home' } },
            {name: '门禁设备管理', path: '/accessSystem/access_manage/deviceManage', component: () => import('@/components/pages/project/cp/functional_module/access_system/device_manage.vue'), meta: { show: true }},
            {name: '通行规则', path: '/accessSystem/access_manage/passRules', component: () => import('@/components/pages/project/cp/functional_module/access_system/pass_rules.vue'), meta: { show: true }},
            {name: '告警', path: '/accessSystem/access_manage/alert', component: () => import('@/components/pages/project/cp/functional_module/access_system/alert.vue'), meta: { show: true }},
            {name: '通行记录', path: '/accessSystem/access_manage/accessRecords', component: () => import('@/components/pages/project/cp/functional_module/access_system/access_records.vue'), meta: { show: true }},
          ]
        }
      ]
    },
    {
      path: '/monitoringSystem',
      name: '考拉智能监控管理系统',
      component: monitoring_system,
      children: [
        {
          name: '智能安防管理',
          path: '/monitoringSystem/securitymonitoring_manage',
          component: securitymonitoring_manage,
          meta: { show: true },
          children: [
            {name: '设备管理', path: '/monitoringSystem/monitoring_system/deviceManage', component: () => import('@/components/pages/project/cp/functional_module/monitoring_system/deviceManage.vue'), meta: { show: true }},
            {name: '监控记录', path: '/monitoringSystem/monitoring_system/monitoringRecord', component: () => import('@/components/pages/project/cp/functional_module/monitoring_system/monitoring_record.vue'), meta: { show: true }},
            {name: '人脸布控', path: '/monitoringSystem/monitoring_system/faceControl', component: () => import('@/components/pages/project/cp/functional_module/monitoring_system/face_control.vue'), meta: { show: true }},
            {name: '区域闯入', path: '/monitoringSystem/monitoring_system/regionalIntrusion', component: () => import('@/components/pages/project/cp/functional_module/monitoring_system/regional_intrusion.vue'), meta: { show: true }},
            {name: '陌生人检测', path: '/monitoringSystem/monitoring_system/strangerDetection', component: () => import('@/components/pages/project/cp/functional_module/monitoring_system/stranger_detection.vue'), meta: { show: true }},
            {name: '告警', path: '/monitoringSystem/monitoring_system/warning', component: () => import('@/components/pages/project/cp/functional_module/monitoring_system/warning.vue'), meta: { show: true }},
            {name: '事件管理', path: '/monitoringSystem/monitoring_system/eventManage', component: () => import('@/components/pages/project/cp/functional_module/monitoring_system/event_manage.vue'), meta: { show: true }},
            {name: '新增事件管理', path: '/monitoringSystem/monitoring_system/addeventManage', component: () => import('@/components/pages/project/cp/functional_module/monitoring_system/add_event_manage.vue'), meta: { show: true }},
            {name: '可疑人员', path: '/monitoringSystem/monitoring_system/SuspiciousPersons', component: () => import('@/components/pages/project/cp/functional_module/monitoring_system/suspicious_persons.vue'), meta: { show: true }},
          ]
        }
      ]
    },
  ]
})



